    <h4 class="fw-bold">Cios w „podbrzusze” Festung Europa</h4>
    <p class="fw-bold">
      Zwycięstwo sprzymierzonych nad niemieckimi i włoskimi wojskami w Afryce Północnej w maju 1943 roku spowodowało, że
      realne wreszcie stało się otwarcie tzw. drugiego frontu w Europie. Wciąż jednak trwały dyskusje, gdzie mają
      lądować główne siły inwazyjne: na brzegach Morza Śródziemnomorskiego czy kanału Le Manche.
    </p>
    <p>
      Zarówno w zdobytym Tunisie, jak i w Wielkiej Brytanii zbierały się alianckie wojska i ich dowódcy czekali na
      sygnał do ataku. Amerykanie, a szczególnie generał George C. Marshall, ówczesny szef Sztabu Generalnego US Army,
      postulowali, by uderzyć z Anglii przez kanał La Manche i z Francji ruszyć ku Berlinowi. Wielkim zwolennikiem tej
      opcji był również Stalin, nieustannie domagający się odciążenia Armii Czerwonej przez otwarcie drugiego frontu w
      Europie Zachodniej.
      Natomiast Winston Churchill i jego generałowie nalegali, by główne uderzenie skierować przez Bałkany. Brytyjski
      premier, w charakterystyczny dla siebie sposób, nazwał je „miękkim podbrzuszem Europy”. W rachubach Churchilla
      inwazja z południa Europy pozwalała wyprzedzić Sowietów zdążających w kierunku Wisły i Dunaju, a także
      zabezpieczyć przed widmem komunizmu Jugosławię i Grecję. Ponadto oderwałoby to od III Rzeszy jej głównych
      sojuszników w Europie: Włochy, Rumunię, Bułgarię i Węgry, jak też zaktywizowało Turcję do opowiedzenia się po
      stronie aliantów.
      Na konferencji w Casablance w styczniu 1943 roku Churchill dał się jednak przekonać Franklinowi D. Rooseveltowi i
      przystał na kompromis. Przestał upierać się przy Bałkanach na rzecz inwazji na Francję w 1944 roku, lecz pod
      warunkiem, że wcześniej na południowe Włochy przeprowadzone zostanie uderzenie pomocnicze. Wyeliminowanie Włoch z
      wojny zmusiłoby Niemców do przerzucenia części swych wojsk na Półwysep Apeniński, a tym samym spowodowałoby
      rozproszenie ich sił w Europie. Churchill liczył też po cichu, że szybki sukces we Włoszech pozwoli mu jednak
      zrealizować scenariusz bałkański i umocnić brytyjskie wpływy w basenie Morza Śródziemnego.

    </p>
    <h4 class="fw-bold"> Sycylijskie lato</h4>
    <p>
      W maju 1943 roku, podczas konferencji w Waszyngtonie zapadły ostateczne decyzje o desancie na Włochy, który ukryto
      pod kryptonimem „Trident”. Jego preludium miało być lądowanie na Sycylii, czyli operacja „Husky”. 10 lipca 1943
      roku na plażach Sycylii lądowały amerykańska 7 Armia generała George’a S. Pattona i brytyjska 8 Armia generała
      Bernarda L. Montgomery’ego. Inwazję z morza poprzedził nocny desant powietrzny i spadochroniarze jako pierwsi
      przekonali się jak kapryśna i zdradliwa może być włoska pogoda. Silny wiatr rozrzucił ich daleko od stref zrzutu i
      jeszcze przez kilka następnych dni wielu z nich przedzierało się przez nieprzyjacielskie linie. Także Włosi, a
      zwłaszcza Niemcy zaskoczyli aliantów twardą obroną, umiejętnie dowodzoną przez marszałka Alberta Kesselringa.
      W skalistym i pociętym wąwozami terenie włoscy i niemieccy żołnierze walczyli zacięcie i umiejętnie. Amerykanie i
      Brytyjczycy zdobywali każdą pozycję po zupełnym zniszczeniu broniących się. Jednak pod koniec lipca marszałek
      Kesselring zrozumiał, że wyspy nie da się utrzymać. Z każdym dniem rosła przewaga sprzymierzonych, a zwłaszcza
      działania generała Pattona zaczęły zagrażać odcięciem od Messyny oddziałów broniących uparcie masywu Etny. W końcu
      Kesselring dał rozkaz ewakuacji Sycylii, która rozpoczęła się w pierwszych dniach sierpnia. Przez Cieśninę
      Messyńską do Kalabrii zdołało ewakuować się około 45 tysięcy Niemców i około 60 tysięcy Włochów.
      17 sierpnia do Mesyny wkroczyły czołówki pancerne generała Pattona, a ich śladem przybyli też czołgiści
      Montgomery’ego z 8 Armii. Zdobycie miasta wieńczyło 38 dni zażartych walk o Sycylię, podczas których alianci
      stracili w zabitych, rannych i zaginionych 20 tysięcy ludzi. Wojska włosko-niemieckie miały kilkanaście tysięcy
      poległych, a 150 tysięcy, głównie Włochów, dostało się do niewoli. Natychmiast po zajęciu Mesyny, sprzymierzeni
      rozpoczęli ostrzeliwanie nieprzyjacielskich pozycji i baterii nadbrzeżnych w Kalabrii. Do akcji weszły też
      alianckie pancerniki, krążowniki i niszczyciele, które swym ogniem oznajmiały Niemcom i Włochom, że niedługo ruszy
      inwazja na Półwysep Apeniński.
    </p>
    <h4 class="fw-bold"> Pęknięcie faszystowskiej osi</h4>
    <p>
      3 września 1943 roku dwie brytyjskie dywizje z 8 Armii przepłynęły Cieśninę Mesyńską i bez trudu zajęły „czubek
      włoskiego buta” w rejonie Kalabrii. Wojska niemieckie i włoskie szybko wycofały się w głąb półwyspu. Niemcy
      spodziewali się alianckiego desantu, ale zaskoczyła ich niespodziewana wolta Włochów – obalenie Mussoliniego,
      kapitulacja i przejście na stronę sprzymierzonych. Szybko i zdecydowanie jednak odpowiedzieli na tą dekonstrukcję
      tzw. paktu osi. Wojska niemieckie błyskawicznie opanowały garnizony i stanowiska armii włoskiej. Każdy opór
      likwidowany był siłą, a zaskoczeni Włosi byli rozbrajani i brani do niewoli. Tysiące żołnierzy włoskich trafiło do
      obozów jenieckich i koncentracyjnych, wielu zamordowano.
      O tym, jak skuteczny nadal jest Wehrmacht w Italii alianci przekonali się już 9 września pod Salerno, gdzie na
      lądujące na plażach oddziały amerykańskie i brytyjskie spadła lawina żelaza, wystrzelona przez niemieckie czołgi.
      Od katastrofy uratowało wtedy atakujących ich lotnictwo, które osłabiło pancerne uderzenie. Jasnym się jednak
      stało, że marsz przez „włoski but” nie będzie spacerem.
      Do 15 września niemieckie natarcia pod Salerno straciły swój impet w ogniu alianckich samolotów szturmowych i
      bombowców. Niemcy jednak cofali się na północ powoli i w sposób zorganizowany – kolejny raz udowadniali, że są
      mistrzami obrony. Tymczasem brytyjska 8 Armia maszerowała z Kalabrii i jej lewe skrzydło połączyło się z wojskami
      desantowymi na południe od Salerno, a lewe ruszyło na Foggię. Natomiast Amerykanie spod Salerno uderzyli na
      Neapol, w którym wybuchło antyniemieckie powstanie. Na początku października miasto było już w rękach żołnierzy
      generała Marka Clarka, dowódcy amerykańskiej 5 Armii. Podobnym sukcesem zakończyły się walki o Foggię, gdzie
      zdobyto wielkie lotnisko. Natomiast w Neapolu sprawnie uruchomiono port, który bardzo przydał się aliantom podczas
      całej kampanii włoskiej.
    </p>
    <h4 class="fw-bold">„Słoneczna Italia” – to kpina!</h4>
    <p>
      Niestety, we Włoszech nastała jesień, a wraz z nią okropna pogoda, która sprzyjała obrońcom, czyli Niemcom, a
      działania armii alianckich zmieniła w prawdziwy koszmar. Żołnierze, chłostani deszczem i wiatrem, taplający się w
      błocie z ironią powtarzali slogany o „słonecznej Italii”. Głównodowodzący siłami sprzymierzonych, generał Dwight
      Eisenhower wspominał: „Nawet gdybyśmy nie natrafili na opór ze strony nieprzyjaciela, posuwanie się wobec
      zniszczonych torów kolejowych, zburzonych mostów, wysadzonych w powietrze odcinków szos byłoby utrudnione. Sam
      teren, pocięty dużymi i małymi rzekami, które biegły w poprzek szlaków naszego natarcia idealnie nadawał się do
      walk obronnych. Niektóre rzeki były tak kręte, że musieliśmy je kilkakrotnie przekraczać”.
      Otóż to. A za zakrętem każdej drogi i rzeki, czekali na alianckich żołnierzy coraz silniejsi Niemcy. Brytyjczycy z
      8 Armii szli coraz wolniej, aż w końcu utknęli na tzw. linii zimowej – niemieckich pozycji obronnych, których
      głównymi elementami były górskie rzeki Garigliano i Sangro oraz szczyty Apeninów. Pozycje na przełęczach górskich
      w jesiennej aurze wydawały się aliantom nie do zdobycia, więc ich siły rozeszły się w dwie strony: amerykańska 5
      Armia przedzierała się przez pozycje nieprzyjaciela wzdłuż brzegów Morza Tyreńskiego, a brytyjska 8 Armia posuwała
      się brzegiem Adriatyku. Leżące między nimi Apeniny całkowicie uniemożliwiały współpracę między nimi, a każda tego
      próba kończyła się ciężkimi stratami w ogniu kontrolujących góry niemieckich spadochroniarzy i strzelców
      alpejskich.
      Niemcy na południu Włoch rośli też w siłę dzięki napływającym z północy kraju i Niemiec nowym dywizjom. Dla
      alianckiego dowództwa stało się jasne, że wyeliminowanie Włoch jako sojusznika III Rzeszy tylko na chwilę
      zachwiało wciąż potężnymi i zaprawionymi w bojach wojskami niemieckimi. Postanowiono wezwać posiłki: z Afryki
      Północnej przetransportowano między innymi Francuski Korpus Ekspedycyjny i postanowiono, że do Włoch przerzucony
      zostanie również 2 Korpus Polski. Ponadto ściągnięto 15 Strategiczną Flotę Powietrzną.
    </p>
    <h4 class="fw-bold">Góra Miliona Dolarów</h4>
    <p>
      Jeden z brytyjskich korespondentów wojennych pisał z Włoch pod koniec 1943 roku: „Nasi ludzie sforsowali Sangro,
      ale za Sangro było Moro, za Moro było Foro, za Foro zaś była Pescara…”. I końca nie było widać. Zanim jednak 8
      Armia dotarła do rzeki Pescara i miasta o tej samej nazwie na wybrzeżu Adriatyku, musiała zdobyć niewielką
      mieścinę Ortone. To zdanie przypadło 1 Dywizji Kanadyjskiej i Kanadyjczycy właśnie pierwsi przekonali się, co
      nieprzyjaciel przygotował na drodze do Rzymu. Brytyjski weteran II wojny światowej i pisarz, tak przedstawił tę
      batalię: „Trzeba tu było zdobywać każdą ulicę, każdy dom i nawet każde piętro. [Kanadyjczykom – PK] Zajęło […] to
      tydzień, spędzili w ten sposób Boże Narodzenie i w końcu zwyciężyli. Ale to był koniec wysiłku 8 Armii. Pescara
      leżała o dziesięć mil, pogoda i teren były gorsze niż kiedykolwiek i armia musiała chwilowo opuścić kotwicę”.
      Po przeciwległej stronie Półwyspu Apenińskiego maszerowała amerykańska 5 Armia. Amerykanie, podobnie jak
      Brytyjczycy, walczyli z błotem i coraz twardszym nieprzyjacielem. Z wielkim trudem udało im się zdobyć Monte
      Camino. W walkach o to wzgórze artyleria wystrzelała 200 tysięcy pocisków. Amerykańscy żołnierze nazwali je więc
      Górą Miliona Dolarów. 15 stycznia 1944 roku w rejonie drogi Neapol – Rzym zdobyli Monte Trocchio. Licząc więc
      przestrzeń i czas od zdobycia Neapolu, przejście kilkudziesięciu kilometrów zajęło aliantom dwa miesiące… A przed
      nimi wyrosły pasma gór Maiella, Abruzzi i Aurunci, przedzielające półwysep od Adriatyku do Morza Tyrreńskiego. Tu
      Niemcy wykuli i wybetonowali dwie potężne linie obronne: Gustawa i Hitlera. Epicentrum piekła czekało na
      sprzymierzonych w Cassino i okolicy.
    </p>
